<template>
  <div class="page main" :class="{black: currentPage !== 'title'}">
    <Logo v-on:navigate="changePage" :current="currentPage" key="logo" :blur="blur"></Logo>
    <Name v-on:navigate="changePage" :current="currentPage" key="name" :blur="blur"></Name>
    <Navigation v-on:navigate="changePage" :current="currentPage" key="navigation" :blur="blur"></Navigation>
    <transition name="title-fade">
      <TitlePage v-if="currentPage === 'title'" :current="currentPage"></TitlePage>
    </transition>
    <transition name="title-fade">
      <WorkPage v-if="currentPage === 'work'" v-on:blur="setBlur($event)"></WorkPage>
    </transition>
    <transition name="title-fade">
      <BioPage v-if="currentPage === 'bio'"></BioPage>
    </transition>
  </div>
</template>

<script>
import Name from "../components/Name.vue";
import Logo from "../components/Logo.vue";
import TitlePage from "../components/TitlePage.vue";
import WorkPage from "../components/WorkPage.vue";
import BioPage from "../components/BioPage.vue";
import Navigation from "../components/Navigation.vue";
import Vue from 'vue';

export default {
  components: {
    Name,
    Logo,
    TitlePage,
    WorkPage,
    BioPage,
    Navigation
  },
  data: function() {
    return {
      currentPage: "",
      blur: false,
    };
  },
  mounted() {
    this.currentPage = "title";
  },
  methods: {
    changePage(event) {
      this.currentPage = event;
    },
    setBlur(event){
      Vue.set(this, 'blur', event);
    }
  }
};
</script>

<style lang="scss" scoped>
.main {
  background: var(--white);
  transition: background 1000ms ease-in-out;

  &.black {
    background: var(--black);
  }
}

.title-fade-enter,
.title-fade-leave-to {
  opacity: 0;
}

.title-fade-enter-active,
.title-fade-leave-active {
  transition: opacity 1000ms ease-in-out;
}
</style>