<template>
  <div class="page container" :class="{blur: showGallery === true || showPhotos === true}">
    <Selector v-on:select="select"></Selector>
    <div class="dots">
      <div class="dot" v-for="i in 8" :key="i"></div>
    </div>
    <Thumbnails
      class="thumbnail"
      :active="(i - 1) === activePage"
      :class="{inactive: (i - 1) !== activePage}"
      v-for="i in numPages"
      :key="i - 1"
      :index="i - 1"
      v-on:photos="photosShow"
      v-on:inspect="inspect(i - 1, $event)"
      :inspecting="inspecting"
    ></Thumbnails>
    <Inspect
      :inspecting="inspecting"
      :thumb="inspectedThumbnail"
      v-on:stop="stopInspecting"
      v-on:galleryShow="galleryShow"
    ></Inspect>
    <transition name="fade">
      <Photos v-if="showPhotos" class="gallery" v-on:photosHide="photosHide"></Photos>
    </transition>
    <Gallery
      v-if="showGallery"
      :galleryIndex="inspectedThumbnail.col"
      class="gallery"
      v-on:galleryHide="galleryHide"
    ></Gallery>
  </div>
</template>

<script>
import Selector from "../components/Selector.vue";
import Thumbnails from "../components/Thumbnails.vue";
import Inspect from "../components/Inspect.vue";
import Gallery from "../components/Gallery.vue";
import Photos from "../components/Photos.vue";
import Vue from "vue";

export default {
  data: () => {
    return {
      options: {},
      numPages: 4,
      activePage: 0,
      inspecting: false,
      inspectedThumbnail: { row: 0, col: 0 },
      showGallery: false,
      showPhotos: false
    };
  },
  components: {
    Selector,
    Thumbnails,
    Inspect,
    Gallery,
    Photos
  },
  props: {
    active: Boolean
  },
  methods: {
    select($event) {
      Vue.set(this, "activePage", $event);
      this.stopInspecting();
    },
    inspect(row, col) {
      console.log(row, col);
      Vue.set(this, "inspectedThumbnail", {
        row,
        col
      });
      Vue.set(this, "inspecting", true);
    },
    stopInspecting() {
      Vue.set(this, "inspecting", false);
    },
    galleryShow() {
      Vue.set(this, "showGallery", true);
      this.$emit("blur", true);
    },
    galleryHide() {
      Vue.set(this, "showGallery", false);
      this.$emit("blur", false);
    },
    photosShow() {
      Vue.set(this, "showPhotos", true);
      this.$emit("blur", true);
    },
    photosHide() {
      Vue.set(this, "showPhotos", false);
      this.$emit("blur", false);
    }
  }
};
</script>

