  background: var(--black);
  &.blur *:not(.gallery) {
    filter: blur(50px);
    transition: filter 200ms ease-in-out;
    opacity: 0.5;
  }
}

.dots {
  position: absolute;
  top: 200px;
  left: 287px;
  height: 80px;
  width: 7px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  .dot {
    height: 3px;
    width: 3px;
    border-radius: 50%;
    border: 1px solid var(--white);
  }
}

.fade-enter-active {
  transition: opacity 200ms ease-in-out;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@media only screen and (max-width: 1280px) {
  .dots {
    display: none;
  }
}
</style>