import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import VueFullPage from 'vue-fullpage.js'

Vue.use(VueRouter)
Vue.use(VueFullPage)

const routes = [
  {
    path: '/',
    name: 'main',
    component: Main
  },
]

const router = new VueRouter({
  routes
})

export default router
